    &Clue {
        position: relative;
        color: transparent;

        &::before,&::after {
            display: block;
            position: absolute;
            opacity: 1;
            visibility: visible;
            // margin-top: 20px;
        }

        &::before {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #FFF;
            text-align: left;
            width: 280px;
            height: 152px;
            top: 40px;
            left: -56px;
            padding: 29px 25px;
            background: linear-gradient(117.84deg, #1D346A 2.84%, #031634 67.28%);
            border-radius: 30px;
            content: attr(data-title);
            box-sizing: border-box;
            z-index: 9;
            transition: opacity 1.0s, margin-top 1.0s;
        }

        &::after {
            content: "";
            border-bottom: 10px solid #1D346A;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            top: 30px;
            left: 16px;
            transition: opacity 1.0s, margin-top 1.0s;
        }

        // &:hover::before,&:focus::before,&:hover::after,&:focus::after {
        //     opacity: 1;
        //     visibility: visible;
        //     margin-top: 0;
        // }
    }

    &Clue2 {
        position: relative;

        &::before {
            display: block;
            position: absolute;
            opacity: 1;
            visibility: visible;
            // margin-top: 40px;
            font-weight: 400;
            font-size: 12px;
            line-height: 16px;
            color: #fff;
            text-align: center;
            width: 280px;
            top: 128px;
            left: -57px;
            content: attr(data);
            box-sizing: border-box;
            z-index: 19;
            transition: opacity 2.0s, margin-top 1.0s;
        }

        // &:hover::before,&:focus::before {
        //     opacity: 1;
        //     visibility: visible;
        //     margin-top: 0;
        //     z-index: 99;
        // }
    }

    &ClueReports {
        position: relative;
        color: transparent;

        &::before,&::after {
            display: block;
            position: absolute;
            opacity: 1;
            visibility: visible;
            // margin-top: 20px;
        }

        &::before {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #FFF;
            text-align: left;
